<template>
  <button
    type="button"
    :class="[buttonSizeClassName, buttonStyleClassName]"
    @click="onClickBtn()">
    {{ text }}
  </button>
</template>

<script>
export default {
  name:'Button',
  props:{
    btnSize: String,
    btnStyle: String,
    text: String,
    routePath: String,
  },
  computed:{
    buttonSizeClassName(){
      return 'btn_'+this.btnSize;
    },
    buttonStyleClassName(){
      return 'btn_'+this.btnStyle;
    }
  },
  methods:{
    onClickBtn(e){
      if( this.routePath ){
        this.$router.push({ path: this.$routerPath[this.routePath] });
        return;
      }
      this.$emit('onClickBtn',e);
    }
  }
}
</script>
<style scoped>
.btn_small{display:inline-block;min-width:56px;height:32px;padding:8px 14px 10px;border-radius:6px;font-family:'NotoSansKR_M', sans-serif;font-size:12px;line-height:14px;vertical-align:top}
.btn_medium{display:inline-block;height:36px;padding:9px 14px 11px;border-radius:6px;font-family:'NotoSansKR_M', sans-serif;font-size:14px;line-height:16px;vertical-align:top}
.btn_large{display:inline-block;width:100%;height:40px;padding:10px 0 12px;border-radius:6px;font-family:'NotoSansKR_M', sans-serif;font-size:14px;line-height:16px;vertical-align:top}
.btn_xlarge{display:inline-block;width:100%;height:50px;padding:14px 20px 16px;border-radius:6px;font-family:'NotoSansKR_M', sans-serif;font-size:16px;line-height:20px;color:#222;vertical-align:top}

.btn_primary{font-family:'NotoSansKR_B', sans-serif;border:1px solid #d20d3d;background-color:#ed164b;color:#fff}
.btn_primary:disabled{border:1px solid #d2a2ae;background-color:#f5bdca}
.btn_secondary{background-color:#222;color:#fff}
.btn_secondary:disabled{background-color:#555;color:#bbb}
.btn_tertiary{border:1px solid #e1e1e1;background-color:#fff;color:#222}
.btn_tertiary:disabled{background-color:#fbfbfb;color:#bbb}

.btn_naver{border:1px solid #15c654;background-color:#19ce60;color:#fff}
.btn_kakao{border:1px solid #ccb802;background-color:#fee500;color:#191919}
.btn_facebook{border:1px solid #29487d;background-color:#4267b2;color:#fff}
.btn_goole{border:1px solid #dc594a;background-color:#df4f3c;color:#fff}

button + button{margin-top:8px}
.desc_error + button{margin-top:20px}

.group_btn_type2 button{min-width:100px}
.group_btn_type2 button + button{margin-top:0;margin-left:8px}

/* 모바일 */
@media all and (max-width:600px){
  .group_btn_type2 button:nth-child(1){width:calc(30% - 4px);min-width:inherit}
  .group_btn_type2 button:nth-child(2){width:calc(70% - 4px);min-width:inherit}
}
</style>