<template>
  <div id="zipproHead" class="z_head" role="banner">
    <button v-if="isGnb" type="button" :class="['btn_gnb',{ acitve : isActiveGnb }]" @click="$emit('onClickGnb')">
      <span class="icon_hamber">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </span>
    </button>
    <h1 class="tit_logo">
      <router-link :to="$routerPath.HOME">
        <img src="/assets/images/logo.png" class="img_g" alt="집 pro">
      </router-link>
    </h1>
    <div class="util_head">
      <Button
        v-if="!userData.userName"
        btnSize="medium"
        btnStyle="secondary"
        text="로그인"
        routePath="LOGIN" />
      <button
        v-if="userData.userName"
        class="btn_util btn_notify">
        <span class="ico_zippro ico_notify_head">알림</span>
        <span v-if="userData.notificationCount" class="txt_util txt_notify">{{ userData.notificationCount }}</span>
      </button>
      <button
        v-if="userData.userName"
        class="btn_util btn_profile">
        <span class="ico_zippro ico_profile_head">사용자</span>
        <span class="txt_util">{{ userData.userName }}</span>
      </button>
    </div>
    <Gnb
      v-if="isGnb"
      :isActiveGnb="isActiveGnb"
      :userData="userData"
      @onClickCloseGnb="$emit('onClickCloseGnb')"/>
  </div>
</template>
<script>
import Gnb from '@/components/layout/header/Gnb';
import Button from '@/components/common/button/Button';

export default {
  name:'Header',
  components:{
    Gnb,
    Button,
  },
  props:{
    isActiveGnb: Boolean,
    isGnb: Boolean,
    userData: Object,
  }
};
</script>
<style scoped>
.z_head{position:fixed;top:0;left:0;right:0;z-index:110;padding-right:20px;background-color:rgba(255,255,255,.7);backdrop-filter:blur(5px);box-shadow:0 1px 4px 0 rgba(0,0,0,.1)}
.z_head:after{display:block;visibility:hidden;height:0;font-size:0;clear:both;content:''}

/* gnb 버튼 */
.btn_gnb{float:left;padding:23px 20px;border-right:1px solid #e1e1e1;background-color:#f9f9f9;transition:background-color 0.2s}
.btn_gnb.acitve{background-color:#ED164B;border:0 none}
/* hamer 아이콘 */
.icon_hamber{position:relative;display:block;width:20px;height:14px;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .2s ease-in-out;
  -moz-transition: .2s ease-in-out;
  -o-transition: .2s ease-in-out;
  transition: .2s ease-in-out
}
.icon_hamber span{display:block;position:absolute;height:2px;width:100%;background-color:#ED164B;border-radius:2px;opacity:1;left:0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}
.icon_hamber span:nth-child(1){top:0px}
.icon_hamber span:nth-child(2),
.icon_hamber span:nth-child(3){top:7px}
.icon_hamber span:nth-child(4){top:14px}
/* hamber 아이콘 - 활성화 */
.btn_gnb.acitve .icon_hamber span{background-color:#fff}
.btn_gnb.acitve .icon_hamber span:nth-child(1){top:7px;width:0%;left:50%}
.btn_gnb.acitve .icon_hamber span:nth-child(2){
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg)
}
.btn_gnb.acitve .icon_hamber span:nth-child(3){
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg)
}
.btn_gnb.acitve .icon_hamber span:nth-child(4){top:7px;width:0%;left:50%}

/* 로고 */
.tit_logo{float:left;margin-left:20px;padding:15px 0}
.tit_logo .img_g{display:block;width:47px;height:30px}

/* 우측 util */
.util_head{float:right;padding:12px 0}
.util_head .btn_util{display:inline-block;padding:9px 10px;vertical-align:top}
.util_head .btn_util .ico_zippro{margin-right:4px}
.util_head .btn_util .txt_util{display:inline-block;margin:2px 0;font-family:'NotoSansKR_M', sans-serif;font-size:12px;line-height:14px;color:#555;vertical-align:top}
.util_head .btn_util + .btn_util{margin-left:8px}

/* 모바일 */
@media all and (max-width:600px){
  .util_head .btn_util{padding:9px 0}
  .util_head .btn_util .ico_zippro{margin-right:2px}
  .util_head .btn_notify{padding:9px 8px}
  .util_head .btn_util .ico_notify_head{margin-right:-5px}
  .util_head .btn_util .txt_notify{width:7px;height:7px;margin:0 0 0;border-radius:7px;font-size:0;line-height:0;background-color:#ED164B}
  .util_head .btn_util + .btn_util{margin-left:12px}
}
</style>