<template>
  <div :class="['inner_app',{ inner_middle : isMiddleMode , inner_has_gnb : isGnb, inner_active_gnb : isGnb && isActiveGnb }]" :style="cssProps">
    <div id="zipproWrap" class="wrap_zippro" ref="wrapZippro">
      <HeaderMiddle
        v-if="isMiddleMode"/>
      <Header
        v-else
        :isGnb="isGnb"
        :isActiveGnb="isActiveGnb"
        :userData="userData"
        @onClickGnb="onClickGnb"
        @onClickCloseGnb="onClickCloseGnb"/>
      <hr class="hide">

      <Content :class="contClassName">
        <slot/>
      </Content>
      <hr class="hide">

      <Footer v-if="isFoot"/>
    </div>
    <slot name="popup" />
    <ToastPopup
      v-if="isToastPopup"
      :toastMsg="toastMsg"
      @onComplete="onCompleteToast" />
  </div>
</template>

<script>
import Header from '@/components/layout/header/Header';
import HeaderMiddle from '@/components/layout/header/HeaderMiddle';
import Content from '@/components/layout/content/Content';
import Footer from '@/components/layout/footer/Footer';
import ToastPopup from '@/components/layout/popup/ToastPopup';

import { mapState } from 'vuex';

export default {
  name:'PageWithLayout',
  components:{
    Header,
    HeaderMiddle,
    Content,
    Footer,
    ToastPopup,
  },
  props:{
    isMiddleMode:{
      type: Boolean,
      default: false,
    },
    bgColor:{
      type:String,
      default: '#fff',
    }
  },
  data(){
    return{
      middelMinHeight: 0,
      isGnb: false,
      isFoot: true,
      userData: {},
    }
  },
  mounted(){
    this.setMobile();
    this.setGnb();
    this.setFoot();
    this.getUserData();
  },
  watch:{
    windowWidth(){
      this.setMobile();
    }
  },
  computed:{
    contClassName(){
      return `cont_${this.$route.meta.menuId}`;
    },
    cssProps(){
      var obj = {};
      obj = {
        '--bgColor': this.bgColor,
      }
      // gnb 유무에 따른 여백
      if(this.isGnb){
        obj['--paddingLeft'] = '60px';
        if(this.isActiveGnb){
          obj['--paddingLeft'] = '180px';
        }
      }
      // foot 유무에 따른 여백
      if(this.isFoot){
        obj['--paddingBottom'] = '100px';
      }
      return obj;
    },
    ...mapState({
      isActiveGnb: state => state.gnb.isActiveGnb,
      isToastPopup: state => state.toast.isToastPopup,
      toastMsg: state => state.toast.toastMsg,
      windowWidth: state => state.viewport.windowWidth,
    }),
  },
  methods:{
    setMobile(){
      if(this.windowWidth <= 600){
        this.onClickCloseGnb();
      }
    },
    // (임시) 사용자정보
    getUserData(){
      if(!this.isGnb) return;
      this.userData = { userType:'분양사무소', userName: '김명식', paymentLeftDay: 888, notificationCount: '99+' }
    },
    // gnb 유무 - 권한필요페이지가 아닐경우만 노출
    setGnb(){
      const { requiresAuth } = this.$route.meta;
      this.isGnb = requiresAuth != undefined ? requiresAuth : true;
    },
    onClickGnb(){
      // this.isActiveGnb = !this.isActiveGnb;
      this.$store.dispatch('CHANGE_GNB_ACTION', !this.isActiveGnb);
    },
    onClickCloseGnb(){
      this.$store.dispatch('CHANGE_GNB_ACTION', false);
      // this.isActiveGnb = false;
    },
    // foot 유무 - router/index.js 에서 페이지별 설정
    setFoot(){
      const { isFoot } = this.$route.meta;
      this.isFoot = isFoot != undefined ? isFoot : true;
    },
    onCompleteToast() {
      this.$store.dispatch('END_TOAST_ACTION');
    },
  },
}
</script>
<style scoped>
.wrap_zippro{position:relative;height:auto;min-height:100%;padding-bottom:var(--paddingBottom);padding-left:var(--paddingLeft);box-sizing:border-box;transition:padding-left .2s}

/* gnb 여백 */
.inner_active_gnb .wrap_zippro{padding-left:180px}

/* 가운데 정렬 페이지 */
.inner_middle{position:relative;height:auto;background-color:var(--bgColor);font-size:0;white-space:nowrap;text-align:center}
.inner_middle:after{display:inline-block;height:100vh;min-height:inherit;vertical-align:middle;content:''}
.inner_middle .wrap_zippro{position:static;display:inline-block;width:100%;max-width:400px;min-height:inherit;padding-top:120px;vertical-align:middle;white-space:normal;text-align:left}
.inner_middle .cont_sub{min-height:inherit;padding:40px 24px}

/* 모바일 */
@media all and (max-width:600px){
  .wrap_zippro{padding-left:0}

  /* gnb 여백 */
  .inner_active_gnb .wrap_zippro{padding-left:0}

  /* 가운데 정렬 페이지 */
  .inner_middle .wrap_zippro{padding-top:50px}
  .inner_middle .cont_sub{padding:30px 24px}
}
</style>