<template>
  <li :class="['item_gnb',{ active: selected }]">
    <button type="button" class="link_gnb" @click="onClickGnb">
      <span :class="['ico_zippro', 'ico_gnb_'+menuId]"></span>
      <span class="txt_gnb">{{ title }}</span>
    </button>
    <div :class="['lnb_zippro',{ active: isActiveLnb }]" v-if="hasSub">
      <ul class="list_lnb">
          <li v-for="item in arrSub" :key='item.menuId' :class="['item_lnb',{active: selectedSubId === item.menuId }]">
            <router-link :to='item.link' class="link_lnb">{{ item.title }}</router-link>
          </li>
      </ul>
    </div>
  </li>
</template>

<script>
export default {
  name: 'MenuItem',
  props: {
    title: String,
    link: String,
    arrSub: Array,
    selected: Boolean,
    selectedSubId: String,
    menuId: String,
    isActiveGnb: Boolean,
  },
  data(){
    return{
      isActiveLnb: false,
    }
  },
  watch:{
    isActiveGnb(){
      if(this.selected && this.isActiveGnb){
        this.isActiveLnb = true
      }
    }
  },
  created(){
    if(this.selected && this.isActiveGnb){
      this.isActiveLnb = true
    };
  },
  computed:{
    hasSub() {
      return this.arrSub && this.arrSub.length > 0;
    },
  },
  methods:{
    onClickGnb(){
      if(this.hasSub){
        if(!this.isActiveGnb){
          this.$store.dispatch('CHANGE_GNB_ACTION', true);
          this.$router.push({ path : this.link });
        }else{
          this.isActiveLnb = !this.isActiveLnb;
        }
      }else{
        this.$router.push({ path : this.link });
        return;
      }
    }
  }
}
</script>
<style scoped>
.item_gnb{overflow:hidden;width:100%;padding:0 12px;box-sizing:border-box}
.item_gnb + .item_gnb{margin-top:10px}
/* 메뉴 링크 */
.item_gnb .link_gnb{display:block;width:100%;padding:10px 10px;border-radius:35px;font-family:'NotoSansKR_M', sans-serif;font-size:14px;line-height:16px;color:#888;white-space:nowrap;text-align:left;transition:0.05s}
.item_gnb .link_gnb .txt_gnb{display:none;margin-left:5px;padding:0 3px}
/* 메뉴 링크 - hover */
.item_gnb:hover .link_gnb{color:#555}
/* 메뉴 링크 - 활성화 */
.item_gnb.active .link_gnb{font-family:'NotoSansKR_B', sans-serif;background-color:#ED164B;color:#fff}
/* 메뉴 링크 - gnb 활성화 */
.gnb_zippro.acitve .link_gnb{border-radius:8px;}
.gnb_zippro.acitve .txt_gnb{display:inline-block;vertical-align:top}

/* lnb */
.lnb_zippro{overflow:hidden;max-height:0;transition:0.2s}
.lnb_zippro .link_lnb{display:block;padding:4px 0 4px 36px;font-size:12px;line-height:16px;letter-spacing:-0.02em;white-space:nowrap}
.lnb_zippro .link_lnb:hover{text-decoration:underline}

.lnb_zippro .active .link_lnb{font-family:'NotoSansKR_M', sans-serif;color:#ED164B}

.gnb_zippro.acitve .lnb_zippro.active{max-height:500px;padding-top:4px}
</style>