<template>
  <div class="toast_comm" ref="toastComm">
    <p class="desc_toast">{{ toastMsg }}</p>
  </div>
</template>

<script>
export default {
  name: 'ToastPopup',
  props:{
    toastMsg: String,
  },
  data() {
    return {
      timeoutID0: null,
      timeoutID1: null,
      timeoutID2: null,
    }
  },
  mounted() {
    this.toastMotion();
  },
  beforeUnmount() {
    if (this.timeoutID0) clearTimeout(this.timeoutID0);
    if (this.timeoutID1) clearTimeout(this.timeoutID1);
    if (this.timeoutID2) clearTimeout(this.timeoutID2);
  },
  methods:{
    toastMotion(){
      const _this = this;

      this.timeoutID0 = setTimeout(function(){
        _this.$refs.toastComm.classList.add('on');
        this.timeoutID0 = null;
      }, 100);
      
      this.timeoutID1 = setTimeout(function(){
        _this.$refs.toastComm.classList.remove('on');
        this.timeoutID1 = null;
      }, 2100);
      
      this.timeoutID2 = setTimeout(function(){
        _this.$emit("onComplete");
        this.timeoutID2 = null;
      }, 2500);
    },
  }
}
</script>
<style scoped>
.toast_comm{position:fixed;bottom:-88px;left:0;right:0;z-index:1000;transition:.2s;text-align:center}
.toast_comm .desc_toast{display:inline-block;font-family:'NotoSansKR_M', sans-serif;font-size:14px;line-height:18px;color:#fff;letter-spacing:-.05em;padding:14px 40px;border-radius:4px;background-color:rgba(0,0,0,.7);text-align:center;vertical-align:top}
.toast_comm.on{bottom:48px}
</style>