<template>
  <div id="zipproGnb" role="navigation" :class="['gnb_zippro',{ acitve : isActiveGnb }]" @click.self="$emit('onClickCloseGnb')">
    <div class="inner_gnb">
      <div v-if="userData.userName" class="area_user">
        <dl class="info_user">
          <dt class="screen_out">사용자</dt>
          <dd class="info_profile">
            <div class="profile_head">
              <span class="ico_zippro ico_profile_gnb"></span>
            </div>
            <div class="profile_body">
              <span class="txt_usertype">{{ userData.userType }}</span>
              <span class="txt_username">{{ userData.userName }}</span>
              <span class="txt_unit">님</span>
            </div>
          </dd>
          <!-- <dt class="screen_out">알림</dt>
          <dd class="info_notify">
            <span class="ico_zippro ico_notify"></span>
            <span v-if="userData.notificationCount" class="badge_notify">{{ userData.notificationCount }}</span>
          </dd> -->
          <dt class="screen_out">알림</dt>
          <dd class="info_payment">
            알림 {{ userData.notificationCount }}
          </dd>
          <dt class="screen_out">이용권 잔여일수</dt>
          <dd class="info_payment">
            이용권 D-{{ userData.paymentLeftDay }}
          </dd>
        </dl>
      </div>
      <ul class="list_gnb">
        <MenuItem
          v-for="item in menuData"
          :key='item.menuId' 
          :title='item.title'
          :link='item.link'
          :arrSub='item.arrSub'
          :selected='selectedMenuId === item.menuId'
          :menuId="item.menuId"
          :selectedSubId='selectedMenuSubId'
          :isActiveGnb="isActiveGnb"/>
      </ul>
    </div>
  </div>
</template>

<script>
import MenuItem from './MenuItem.vue';
import { menuData } from '@/constants/menuData.js';

export default {
  name: 'Gnb',
  components:{
    MenuItem
  },
  props:{
    isActiveGnb: Boolean,
    userData: Object
  },
  computed:{
    menuData(){
      return menuData;
    }
  },
  beforeMount() {
    const menuId = this.$route.meta.menuId;
    this.selectedMenuId = menuId;

    const subMenuId = this.$route.meta.subMenuId;
    if( subMenuId ){
      this.selectedMenuSubId = subMenuId;
    }
  },
  data() {
    return {
      selectedMenuId: null,
      selectedMenuSubId: null,
    }
  }
}
</script>
<style scoped>
.gnb_zippro{position:fixed;top:60px;bottom:0;left:0;z-index:100;height:calc(100vh - 60px)}
.gnb_zippro .inner_gnb{width:60px;height:100%;padding:0 0 20px;background:linear-gradient(to bottom, #F0F0F0 0%, #f9f9f9 4px, #f9f9f9 100%);border-right:1px solid #e1e1e1;transition:.2s;}

/* gnb 활성화 */
.gnb_zippro.acitve .inner_gnb{width:180px}

.area_user{overflow:hidden;max-height:0;transition:.02s;white-space:nowrap}
.area_user .info_user{padding:20px 20px;border-bottom:1px solid #e1e1e1}

.area_user .info_profile{overflow:hidden;padding-bottom:14px}
.area_user .profile_head{float:left}
.area_user .profile_head .ico_profile_gnb{margin:16px 6px 2px 0}
.area_user .profile_body{overflow:hidden}
.area_user .profile_body .txt_usertype{display:block;font-family:'NotoSansKR_M', sans-serif;font-size:12px;line-height:14px;color:#555}
.area_user .profile_body .txt_username{display:inline-block;overflow:hidden;max-width:calc(100% - 15px);padding-top:2px;font-family:'NotoSansKR_B', sans-serif;font-size:18px;line-height:20px;color:#555;white-space:nowrap;text-overflow:ellipsis;vertical-align:top}
.area_user .profile_body .txt_unit{display:inline-block;padding:3px 0 1px 0;font-size:16px;line-height:18px;color:#555;vertical-align:top}

/* .area_user .info_notify{position:relative;display:inline-block;padding:4px 18px;vertical-align:top}
.area_user .info_notify .badge_notify{position:absolute;top:2px;left:32px;display:inline-block;min-width:14px;padding:1px 5px 2px;border-radius:10px;font-family:'NotoSansKR_B', sans-serif;font-size:10px;line-height:12px;background-color:#ED164B;color:#fff;box-sizing:border-box;vertical-align:top;text-align:center} */
.area_user .info_payment + dt + .info_payment{margin-top:4px}


.area_user .info_payment{display:block;padding:5px 10px 6px;font-family:'NotoSansKR_B', sans-serif;font-size:12px;line-height:14px;border:1px solid #888;color:#888;text-align:center;box-sizing:border-box}

.gnb_zippro.acitve .area_user{max-height:500px;}

.area_user + .list_gnb{margin-top:12px}
.list_gnb:first-child{padding-top:14px}

/* 모바일 */
@media all and (max-width:600px){
  .gnb_zippro{transition:0.2s}
  .gnb_zippro .inner_gnb{overflow:hidden;width:0}
  .gnb_zippro.acitve{width:100%;background-color:rgba(0,0,0,.5)}
}
</style>